<template>
	<div id="store-basic">
		<public-header title="基本信息"></public-header>
		<div class="content" v-if="store">
			<van-cell-group>
				<van-cell title="门店名称" :value="store.title ? store.title : '未设置'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'title', from: from}})" />
				<van-cell class="store-logo flex-lr" title="门店Logo" :to="util.getUrl({path: '/pages/shop/update', query: {type: 'logo', from: from}})">
					<div slot="right-icon" class="flex ">
						<img :src="store.logo" alt="" v-if="store.logo">
						<span v-else>未设置</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="门店描述" :value="store.content ? store.content : '未设置'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'content', from: from}})" />
				<van-cell title="所属分类" :value="store.category ? store.category : '未设置'" is-link @click="util.$toast('您无法修改此项，如需更换门店分类，请联系平台管理员')" v-if="from == 'waimai'" />
				<template v-if="from == 'haodian'">
					<van-cell title="门店分类" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'haodian_category', from: from}})">
						<div slot="right-icon" class="flex ">
							<span v-if="store.haodian_cid > 0">{{store.haodian_category_cn}}</span>
							<span v-else>未设置</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="好店标签" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'haodian_tags', from: from}})">
						<div slot="right-icon" class="flex ">
							<template v-if="store.haodian_tags && store.haodian_tags.length > 0">
								<van-tag class="haodian-tag bg-info" v-for="(item, index) in store.haodian_tags" :key="index">{{item}}</van-tag>
							</template>
							<span v-else>未设置</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
				</template>
				<van-cell title="营业时间" :value="(store.business_hours && store.business_hours.length > 0) ? store.business_hours_cn : '未设置'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'business_hours', from: from}})"
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="联系电话" :value="store.telephone ? store.telephone : '未设置'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'telephone', from: from}})" />
				<van-cell title="详细地址" :value="store.address ? store.address : '未设置'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'address', from: from}})" />
				<van-cell title="门店实景" :value="(store.thumbs && store.thumbs.length > 0) ? '已上传' + store.thumbs.length + '张' : '待上传'" is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'thumbs', from: from}})" />
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				store: {},
				from: 'waimai'
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'manage/shop/index/info',
					vue: that,
					success: function(result) {
						that.store = result.store;
					}
				});
			}
		},
		created() {

		},
		mounted() {
			if(this.$route.query && this.$route.query.from) {
				let from = this.$route.query.from;
				let arr = ['waimai', 'haodian'];
				if(arr.indexOf(from) != -1) {
					this.from = from;
				}
			}
			console.log('from', this.from);
			this.onLoad();
		}
	}
</script>

<style>
	#store-basic .van-cell.store-logo img{
		width: 80px;
		height: 60px;
		border-radius: 3px;
	}
	#store-basic .haodian-tag{
		margin-left: 5px;
		max-width: 58px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
</style>


